#root {
  width: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Gabriela', Arial, serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #EDE8C9;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.bg-light {
  background: #C8BFA0!important;
}


.wrapper {
  max-width: 1600px;
  margin: 0 auto; }
  .wrapper .switchLang img {
    margin: 5px;
    box-shadow: 2px 2px 10px gray; }
    .wrapper .switchLang img:hover {
      -webkit-transform: scale(1.2, 1.2);
              transform: scale(1.2, 1.2);
      cursor: pointer; }
    .wrapper .switchLang img:active {
      -webkit-transform: scale(1.1, 1.1);
              transform: scale(1.1, 1.1); }

.wrapper-nav {
  width: 100%;
  background: #C8BFA0; }

.navbar {
  max-width: 1200px;
  margin: auto; }
  .navbar h1 {
    margin: 0;
    padding: 0; }

.navbar-nav {
  font-size: 18px; }
  .navbar-nav .nav-item {
    padding-bottom: 0;
    margin-left: 15px; }

.hero {
  display: flex;
  flex-direction: column;
  max-width: 500px;
  padding: 20px;
  box-shadow: 2px 2px 10px gray;
  color: #2b1705; }
  @media screen and (max-width: 640px) {
    .hero {
      min-width: 100%;
      padding: 20px;
      margin-bottom: 10px; } }
  .hero img {
    display: block;
    max-width: 300px;
    margin: 0 auto;
    border-radius: 15px;
    box-shadow: 2px 2px 20px gray; }
    @media screen and (max-width: 640px) {
      .hero img {
        min-width: 80%; } }
  .hero .title {
    font-size: 1.2em;
    text-align: center;
    max-width: 300;
    margin: 0 auto; }
    .hero .title h3 {
      margin-top: 10px; }

@font-face {
  font-family: "New";
  src: url(/codejam5/static/media/nefont.05be004b.ttf); }

.background {
  padding: 10px 20px;
  text-align: center;
  padding-top: 15vmin;
  background-image: url(/codejam5/static/media/bel-mir.75af3aed.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  box-shadow: 2px 2px 10px gray; }
  @media screen and (max-width: 640px) {
    .background {
      padding-top: 0;
      background-image: none; } }
  .background h1,
  .background h2 {
    color: #0e0701;
    text-shadow: 5px 5px 20px white; }
  .background h1 {
    font-size: 6vmin; }
  .background h2 {
    font-size: 4vmin; }

@font-face {
  font-family: 'fontello';
  src: url(/codejam5/static/media/fontello.98641b00.woff);
  font-weight: normal;
  font-style: normal; }

.developers-title {
  text-align: center;
  border-bottom: 1px solid #e1dedb;
  padding: 30px 0 0; }

.developers-title:last-child {
  margin-top: 10px;
  border-top: 1px solid #e1dedb; }

.developers {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: center; }

.developers-teammate {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  margin: 15px;
  font-family: josefin;
  line-height: 1.2;
  background-color: #f8f4f0;
  border-radius: 15px;
  flex-basis: 215px;
  border: 1px solid #e1dedb;
  box-shadow: 0 0 5px rgba(33, 0, 87, 0.5); }

.teammate-contact {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin: 20px;
  font-family: josefin bold;
  line-height: 1.5;
  text-align: center; }

.noselect {
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-user-select: none;
          user-select: none; }

.developers-teammate:nth-child(1) .teammate-foto {
  background: url(/codejam5/static/media/mdgh.092a527a.jpg);
  background-size: contain; }

.developers-teammate:nth-child(2) .teammate-foto {
  background: url(/codejam5/static/media/gaddyya.89a26951.jpg);
  background-size: contain; }

.developers-teammate:nth-child(3) .teammate-foto {
  background: url(/codejam5/static/media/zmichgen.955bb514.jpg);
  background-size: contain; }

.developers-teammate:nth-child(4) .teammate-foto {
  background: url(/codejam5/static/media/zerovers.c26d5d2a.jpg);
  background-size: contain; }

.developers-teammate:nth-child(5) .teammate-foto {
  background: url(/codejam5/static/media/renardby.c4666684.png);
  background-size: contain; }

.teammate-foto {
  width: 150px;
  height: 150px;
  margin: 10px 0;
  border-radius: 50%;
  box-shadow: 0 0 5px rgba(33, 0, 87, 0.5); }

.linked-icon {
  display: inline-block;
  width: 1em;
  margin: 0 .2em;
  font-family: "Fontello", Arial;
  font-style: normal;
  font-weight: normal;
  -webkit-font-feature-settings: normal;
          font-feature-settings: normal;
  font-variant: normal;
  text-decoration: inherit;
  text-align: center;
  text-transform: none;
  line-height: 1em;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.linked-icon:hover {
  color: #e5194c; }

body {
  flex-direction: column;
  display: flex;
  align-items: center;
  margin: 0px; }

.footer {
  position: absolute;
  right: 0;
  padding: 0;
  text-align: center;
  width: 100%;
  height: 40px;
  line-height: 40px;
  background-color: #2F2E2E;
  color: #fff;
  overflow: hidden; }

.portal {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  max-width: 1200px;
  margin: 30px auto 0; }
  @media screen and (max-width: 640px) {
    .portal {
      flex-direction: column; } }

.about-author {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 50px 0;
}

.about-author-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 30px;
}

.about-author-name {
  margin: 0;
}

.about-author-text {
  max-width: 500px;
  text-align: justify;
  margin: 0 20px;
}

.list-books {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 30px;
}
.timeline--wrapper {
  width: 100%;
  background: #2F2E2E;
}

.body p {
  color: #EDE8C9;
}
.image-gallery-thumbnail-inner .active {
  border: none;
}

.image-gallery {
  display: flex;
  max-width: 1100px;
  width: 100%;
  margin: 0 auto;
}

.image-gallery-swipe {
  display: flex;
  justify-content: center;
}

.image-gallery-slide img {
  height: 400px;
  margin: 0 auto;
}

.image-gallery-thumbnail-inner img{
  height: 80px;
}

.image-gallery-content {
  position: relative;
  width: 100%;
  line-height: 0;
  top: 0;
}

.image-gallery-slide-wrapper {
  position: relative;
  width: 100%;
}

.image-gallery-thumbnails-wrapper .bottom {
  max-width: 450px;
  width: 100%;
}

button.image-gallery-left-nav:hover::before,
button.image-gallery-right-nav:hover::before {
  color: #4E362E;
}

.image-gallery-left-nav:focus,
.image-gallery-right-nav:focus {
  outline: none;
}

.image-gallery-thumbnail.active {
  border: 3px solid #4E362E;
}
.youtube-video {
  display: flex;
  justify-content: center;
  margin: 30px auto 0;
  padding: 35px;
  background: #2F2E2E;
}

.bigpicture.link.picture img {
  min-width: 400px;
  width: 100%;
  margin: 0 auto;
  vertical-align: middle;
  border-style: none;
}
.search-input {
  display: flex;
  justify-content: center;
  margin-top: 50px;
}

.search-input input {
  max-width: 400px;
  min-width: 250px;
  width: 100%;
  padding: 5px;
  border-radius: 15px;
  border: 2px solid #8d7e7e;
  outline: none;
}

.authors-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 1200px;
  margin: 50px auto 0;
  box-sizing: border-box;
}

.author-info {
  max-width: 250px;
  width: 100%;
  text-align: center;
}

.author-info:hover {
  cursor: pointer;
}

.author-foto {
  width: 195px;
  height: 190px;
}

.author-name {
  margin-top: 10px;
}

.author-description {
  margin: 10px 0 0;
  text-align: justify;
}

.authors-container .author-link {
  position: relative;
  max-height: 370px;
  height: 100%;
  overflow: hidden;
  margin: 15px;
  text-decoration: none;
  color: black;
}

.text-fade {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  text-align: center;
  height: 3em;
  border-bottom: .75em solid #EDE8C9;
  margin: 0;
  padding: 0;
  background: linear-gradient(to top,#EDE8C9 0,rgba(255,255,255,0) 100%);
}

